/**
 * upload
 * 
 */

layui.define(['lay', 'i18n', 'layer'], function (exports) {
  'use strict';

  var $ = layui.$;
  var lay = layui.lay;
  var layer = layui.layer;
  var i18n = layui.i18n;

  var device = layui.device();
  var hint = layui.hint();

  // 
  var MOD_NAME = 'upload';
  var MOD_INDEX = 'layui_' + MOD_NAME + '_index'; // 

  // 
  var upload = {
    config: {}, // 
    index: layui[MOD_NAME] ? layui[MOD_NAME].index + 10000 : 0, // 
    // 
    set: function (options) {
      var that = this;
      that.config = $.extend({}, that.config, options);
      return that;
    },
    // 
    on: function (events, callback) {
      return layui.onevent.call(this, MOD_NAME, events, callback);
    }
  };

  // 
  var thisModule = function () {
    var that = this;
    var options = that.config;
    var id = options.id;

    thisModule.that[id] = that; // 

    return {
      upload: function (files) {
        that.upload.call(that, files);
      },
      reload: function (options) {
        that.reload.call(that, options);
      },
      config: that.config
    };
  };

  // 
  // var ELEM = 'layui-upload';
  // var THIS = 'layui-this';
  // var SHOW = 'layui-show';
  // var HIDE = 'layui-hide';
  // var DISABLED = 'layui-disabled';

  var ELEM_FILE = 'layui-upload-file';
  var ELEM_FORM = 'layui-upload-form';
  var ELEM_IFRAME = 'layui-upload-iframe';
  var ELEM_CHOOSE = 'layui-upload-choose';
  // var ELEM_DRAG = 'layui-upload-drag';
  var UPLOADING = 'UPLOADING';

  // 
  var Class = function (options) {
    var that = this;
    that.index = ++upload.index;
    that.config = $.extend({}, that.config, upload.config, options);
    that.render();
  };

  // 
  Class.prototype.config = {
    accept: 'images', // images/file/video/audio
    exts: '', // 
    auto: true, // 
    bindAction: '', // 
    url: '', // 
    force: '', //  json
    field: 'file', // 
    acceptMime: '', // 
    method: 'post', //  http 
    data: {}, // 
    drag: true, // 
    size: 0, // 
    number: 0, // 
    multiple: false, //  ie8-9
    text: {
      // 
      'cross-domain': 'Cross-domain requests are not supported', // 
      'data-format-error': 'Please return JSON data format', // 
      'check-error': '', // 
      error: '', // 
      'limit-number': null, //  number  --- function
      'limit-size': null //  size  --- function
    }
  };

  // 
  Class.prototype.reload = function (options) {
    var that = this;
    that.config = $.extend({}, that.config, options);
    that.render(true);
  };

  // 
  Class.prototype.render = function (rerender) {
    var that = this;
    var options = that.config;

    //  elem 
    var elem = $(options.elem);
    if (elem.length > 1) {
      layui.each(elem, function () {
        upload.render(
          $.extend({}, options, {
            elem: this
          })
        );
      });
      return that;
    }

    //  lay-options 
    $.extend(
      options,
      lay.options(elem[0], {
        attr: elem.attr('lay-data') ? 'lay-data' : null //  lay-data 
      })
    );

    //  render reload 
    if (!rerender && elem[0] && elem.data(MOD_INDEX)) {
      var newThat = thisModule.getThis(elem.data(MOD_INDEX));
      if (!newThat) return;

      return newThat.reload(options);
    }

    options.elem = $(options.elem);
    options.bindAction = $(options.bindAction);

    //  id  -  options >  id > 
    options.id = 'id' in options ? options.id : elem.attr('id') || that.index;

    that.file();
    that.events();
  };

  //
  Class.prototype.file = function () {
    var that = this;
    var options = that.config;
    var elemFile = (that.elemFile = $(
      [
        '<input class="' +
          ELEM_FILE +
          '" type="file" accept="' +
          options.acceptMime +
          '" name="' +
          options.field +
          '"',
        options.multiple ? ' multiple' : '',
        '>'
      ].join('')
    ));
    var next = options.elem.next();

    if (next.hasClass(ELEM_FILE) || next.hasClass(ELEM_FORM)) {
      next.remove();
    }

    //ie8/9
    if (device.ie && device.ie < 10) {
      options.elem.wrap('<div class="layui-upload-wrap"></div>');
    }

    that.isFile()
      ? ((that.elemFile = options.elem), (options.field = options.elem[0].name))
      : options.elem.after(elemFile);

    //ie8/9Form
    if (device.ie && device.ie < 10) {
      that.initIE();
    }
  };

  //ie8-9
  Class.prototype.initIE = function () {
    var that = this;
    var options = that.config;
    var iframe = $(
      '<iframe id="' +
        ELEM_IFRAME +
        '" class="' +
        ELEM_IFRAME +
        '" name="' +
        ELEM_IFRAME +
        '" frameborder="0"></iframe>'
    );
    var elemForm = $(
      [
        '<form target="' +
          ELEM_IFRAME +
          '" class="' +
          ELEM_FORM +
          '" method="post" key="set-mine" enctype="multipart/form-data" action="' +
          options.url +
          '">',
        '</form>'
      ].join('')
    );

    //iframe
    $('#' + ELEM_IFRAME)[0] || $('body').append(iframe);

    //
    if (!options.elem.next().hasClass(ELEM_FORM)) {
      that.elemFile.wrap(elemForm);

      //
      options.elem.next('.' + ELEM_FORM).append(
        (function () {
          var arr = [];
          layui.each(options.data, function (key, value) {
            value = typeof value === 'function' ? value() : value;
            arr.push(
              '<input type="hidden" name="' + key + '" value="' + value + '">'
            );
          });
          return arr.join('');
        })()
      );
    }
  };

  //
  Class.prototype.msg = function (content) {
    return layer.msg(content, {
      icon: 2,
      shift: 6
    });
  };

  //
  Class.prototype.isFile = function () {
    var elem = this.config.elem[0];
    if (!elem) return;
    return elem.tagName.toLocaleLowerCase() === 'input' && elem.type === 'file';
  };

  //
  Class.prototype.preview = function (callback) {
    var that = this;
    if (window.FileReader) {
      layui.each(that.chooseFiles, function (index, file) {
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
          callback && callback(index, file, this.result);
        };
      });
    }
  };

  // 
  Class.prototype.upload = function (files, type) {
    var that = this;
    var options = that.config;
    var text = options.text || {};
    var elemFile = that.elemFile[0];

    // 
    var getFiles = function () {
      return files || that.files || that.chooseFiles || elemFile.files;
    };

    // 
    var ajaxSend = function () {
      var successful = 0;
      var failed = 0;
      var items = getFiles();

      // 
      var allDone = function () {
        if (options.multiple && successful + failed === that.fileLength) {
          typeof options.allDone === 'function' &&
            options.allDone({
              total: that.fileLength,
              successful: successful,
              failed: failed
            });
        }
      };

      // 
      var request = function (sets) {
        var formData = new FormData();

        // 
        var resetFileState = function (file) {
          if (sets.unified) {
            layui.each(items, function (index, file) {
              delete file[UPLOADING];
            });
          } else {
            delete file[UPLOADING];
          }
        };

        // 
        layui.each(options.data, function (key, value) {
          value =
            typeof value === 'function'
              ? sets.unified
                ? value()
                : value(sets.index, sets.file)
              : value;
          formData.append(key, value);
        });

        /*
         *  file 
         */

        // 
        if (sets.unified) {
          layui.each(items, function (index, file) {
            if (file[UPLOADING]) return;
            file[UPLOADING] = true; // 
            formData.append(options.field, file);
          });
        } else {
          // 
          if (sets.file[UPLOADING]) return;
          formData.append(options.field, sets.file);
          sets.file[UPLOADING] = true; // 
        }

        // ajax 
        var opts = {
          url: options.url,
          type: 'post', //  post 
          data: formData,
          dataType: options.dataType || 'json',
          contentType: false,
          processData: false,
          headers: options.headers || {},
          success: function (res) {
            // 
            options.unified ? (successful += that.fileLength) : successful++;
            done(sets.index, res);
            allDone(sets.index);
            resetFileState(sets.file);
          },
          error: function (e) {
            // 
            options.unified ? (failed += that.fileLength) : failed++;
            that.msg(
              text['error'] ||
                [
                  'Upload failed, please try again.',
                  'status: ' +
                    (e.status || '') +
                    ' - ' +
                    (e.statusText || 'error')
                ].join('<br>')
            );
            error(sets.index, e.responseText, e);
            allDone(sets.index);
            resetFileState(sets.file);
          }
        };

        // 
        if (typeof options.progress === 'function') {
          opts.xhr = function () {
            var xhr = $.ajaxSettings.xhr();
            // 
            xhr.upload.addEventListener('progress', function (obj) {
              if (obj.lengthComputable) {
                var percent = Math.floor((obj.loaded / obj.total) * 100); // 
                options.progress(
                  percent,
                  options.item ? options.item[0] : options.elem[0],
                  obj,
                  sets.index
                );
              }
            });
            return xhr;
          };
        }
        $.ajax(opts);
      };

      // 
      if (options.unified) {
        request({
          unified: true,
          index: 0
        });
      } else {
        layui.each(items, function (index, file) {
          request({
            index: index,
            file: file
          });
        });
      }
    };

    //  IE 
    var iframeSend = function () {
      var iframe = $('#' + ELEM_IFRAME);

      that.elemFile.parent().submit();

      // 
      clearInterval(Class.timer);
      Class.timer = setInterval(function () {
        var res,
          iframeBody = iframe.contents().find('body');
        try {
          res = iframeBody.text();
        } catch (e) {
          that.msg(text['cross-domain']);
          clearInterval(Class.timer);
          error();
        }
        if (res) {
          clearInterval(Class.timer);
          iframeBody.html('');
          done(0, res);
        }
      }, 30);
    };

    // 
    var forceConvert = function (src) {
      if (options.force === 'json') {
        if (typeof src !== 'object') {
          try {
            return {
              status: 'CONVERTED',
              data: JSON.parse(src)
            };
          } catch (e) {
            that.msg(text['data-format-error']);
            return {
              status: 'FORMAT_ERROR',
              data: {}
            };
          }
        }
      }
      return { status: 'DO_NOTHING', data: {} };
    };

    // 
    var done = function (index, res) {
      that.elemFile.next('.' + ELEM_CHOOSE).remove();
      elemFile.value = '';

      var convert = forceConvert(res);

      switch (convert.status) {
        case 'CONVERTED':
          res = convert.data;
          break;
        case 'FORMAT_ERROR':
          return;
      }

      typeof options.done === 'function' &&
        options.done(res, index || 0, function (files) {
          that.upload(files);
        });
    };

    // 
    var error = function (index, res, xhr) {
      if (options.auto) {
        elemFile.value = '';
      }

      var convert = forceConvert(res);

      switch (convert.status) {
        case 'CONVERTED':
          res = convert.data;
          break;
        case 'FORMAT_ERROR':
          return;
      }

      typeof options.error === 'function' &&
        options.error(
          index || 0,
          function (files) {
            that.upload(files);
          },
          res,
          xhr
        );
    };

    var check;
    var exts = options.exts;
    var value = (function () {
      var arr = [];
      layui.each(files || that.chooseFiles, function (i, item) {
        arr.push(item.name);
      });
      return arr;
    })();

    // 
    var args = {
      // 
      preview: function (callback) {
        that.preview(callback);
      },
      // 
      upload: function (index, file) {
        var thisFile = {};
        thisFile[index] = file;
        that.upload(thisFile);
      },
      // 
      pushFile: function () {
        that.files = that.files || {};
        layui.each(that.chooseFiles, function (index, item) {
          that.files[index] = item;
        });
        return that.files;
      },
      // 
      resetFile: function (index, file, filename) {
        var newFile = new File([file], filename);
        that.files = that.files || {};
        that.files[index] = newFile;
      },
      // 
      getChooseFiles: function () {
        return that.chooseFiles;
      }
    };

    // 
    var send = function () {
      var ready = function () {
        // IE 
        if (device.ie) {
          return device.ie > 9 ? ajaxSend() : iframeSend();
        }
        ajaxSend();
      };
      //  -  false  Promise.reject
      if (typeof options.before === 'function') {
        upload.util.promiseLikeResolve(options.before(args)).then(
          function (result) {
            if (result !== false) {
              ready();
            } else {
              if (options.auto) {
                elemFile.value = '';
              }
            }
          },
          function (error) {
            if (options.auto) {
              elemFile.value = '';
            }
            error !== undefined && hint.error(error);
          }
        );
      } else {
        ready();
      }
    };

    // 
    var typeName =
      {
        file: i18n.$t('upload.fileType.file'),
        images: i18n.$t('upload.fileType.image'),
        video: i18n.$t('upload.fileType.video'),
        audio: i18n.$t('upload.fileType.audio')
      }[options.accept] || i18n.$t('upload.fileType.file');

    // 
    value =
      value.length === 0
        ? elemFile.value.match(/[^/\\]+\..+/g) || [] || ''
        : value;

    // 
    if (value.length === 0) return;

    // 
    switch (options.accept) {
      case 'file': // 
        layui.each(value, function (i, item) {
          if (exts && !RegExp('.\\.(' + exts + ')$', 'i').test(escape(item))) {
            return (check = true);
          }
        });
        break;
      case 'video': // 
        layui.each(value, function (i, item) {
          if (
            !RegExp(
              '.\\.(' + (exts || 'avi|mp4|wma|rmvb|rm|flash|3gp|flv') + ')$',
              'i'
            ).test(escape(item))
          ) {
            return (check = true);
          }
        });
        break;
      case 'audio': // 
        layui.each(value, function (i, item) {
          if (
            !RegExp('.\\.(' + (exts || 'mp3|wav|mid') + ')$', 'i').test(
              escape(item)
            )
          ) {
            return (check = true);
          }
        });
        break;
      default: // 
        layui.each(value, function (i, item) {
          if (
            !RegExp(
              '.\\.(' + (exts || 'jpg|png|gif|bmp|jpeg|svg|webp') + ')$',
              'i'
            ).test(escape(item))
          ) {
            return (check = true);
          }
        });
        break;
    }

    // 
    if (check) {
      that.msg(
        text['check-error'] ||
          i18n.$t('upload.validateMessages.fileExtensionError', {
            fileType: typeName
          })
      );
      return (elemFile.value = '');
    }

    // 
    if (type === 'choose' || options.auto) {
      options.choose && options.choose(args);
      if (type === 'choose') {
        return;
      }
    }

    // 
    that.fileLength = (function () {
      var length = 0;
      var items = getFiles();
      layui.each(items, function () {
        length++;
      });
      return length;
    })();

    if (options.number && that.fileLength > options.number) {
      return that.msg(
        typeof text['limit-number'] === 'function'
          ? text['limit-number'](options, that.fileLength)
          : i18n.$t('upload.validateMessages.filesOverLengthLimit', {
              length: options.number
            }) +
              '<br/>' +
              i18n.$t('upload.validateMessages.currentFilesLength', {
                length: that.fileLength
              })
      );
    }

    // 
    if (options.size > 0 && !(device.ie && device.ie < 10)) {
      var limitSize;

      layui.each(getFiles(), function (index, file) {
        if (file.size > 1024 * options.size) {
          var size = options.size / 1024;
          size = size >= 1 ? size.toFixed(2) + 'MB' : options.size + 'KB';
          elemFile.value = '';
          limitSize = size;
        }
      });
      if (limitSize)
        return that.msg(
          typeof text['limit-size'] === 'function'
            ? text['limit-size'](options, limitSize)
            : i18n.$t('upload.validateMessages.fileOverSizeLimit', {
                size: limitSize
              })
        );
    }

    send();
  };

  //
  Class.prototype.events = function () {
    var that = this;
    var options = that.config;

    // 
    var setChooseFile = function (files) {
      that.chooseFiles = {};
      layui.each(files, function (i, item) {
        var time = new Date().getTime();
        that.chooseFiles[time + '-' + i] = item;
      });
    };

    // 
    var setChooseText = function (files, _filename) {
      var elemFile = that.elemFile;
      // var item = options.item ? options.item : options.elem;
      var value =
        files.length > 1
          ? i18n.$t('upload.chooseText', { length: files.length })
          : (files[0] || {}).name ||
            elemFile[0].value.match(/[^/\\]+\..+/g) ||
            [] ||
            '';

      if (elemFile.next().hasClass(ELEM_CHOOSE)) {
        elemFile.next().remove();
      }
      that.upload(null, 'choose');
      if (that.isFile() || options.choose) return;
      elemFile.after(
        '<span class="layui-inline ' + ELEM_CHOOSE + '">' + value + '</span>'
      );
    };

    /**
     * 
     * @param {File} file
     * @return {boolean}
     */
    var checkFile = function (file) {
      var result = true;
      layui.each(that.files, function (index, item) {
        result = !(item.name === file.name);
        if (!result) return true;
      });
      return result;
    };

    /**
     * 
     * @template {File | FileList} T
     * @param {T} obj
     * @return {T}
     */
    var extendInfo = function (obj) {
      var extInfo = function (file) {
        //
        file.ext = file.name
          .substr(file.name.lastIndexOf('.') + 1)
          .toLowerCase();
        // 
        file.sizes = upload.util.parseSize(file.size);
        // 
      };

      //FileList
      if (obj instanceof FileList) {
        layui.each(obj, function (index, item) {
          extInfo(item);
        });
      } else {
        extInfo(obj);
      }

      return obj;
    };

    /**
     * 
     * @param {FileList} files
     * @return {Array<File>|FileList}
     */
    var getFiles = function (files) {
      files = files || [];
      if (!files.length) return [];
      if (!that.files) return extendInfo(files);
      var result = [];
      layui.each(files, function (index, item) {
        if (checkFile(item)) {
          result.push(extendInfo(item));
        }
      });
      return result;
    };

    // 
    options.elem.off('upload.start').on('upload.start', function () {
      var othis = $(this);

      that.config.item = othis;
      that.elemFile[0].click();
    });

    // 
    if (!(device.ie && device.ie < 10)) {
      options.elem
        .off('upload.over')
        .on('upload.over', function () {
          var othis = $(this);
          othis.attr('lay-over', '');
        })
        .off('upload.leave')
        .on('upload.leave', function () {
          var othis = $(this);
          othis.removeAttr('lay-over');
        })
        .off('upload.drop')
        .on('upload.drop', function (e, param) {
          var othis = $(this);
          var files = getFiles(param.originalEvent.dataTransfer.files);

          othis.removeAttr('lay-over');
          setChooseFile(files);

          options.auto ? that.upload() : setChooseText(files); // 
        });
    }

    // 
    that.elemFile.on('change', function () {
      var files = getFiles(this.files);

      if (files.length === 0) return;

      setChooseFile(files);

      options.auto ? that.upload() : setChooseText(files); // 
    });

    // 
    options.bindAction.off('upload.action').on('upload.action', function () {
      that.upload();
    });

    // 
    if (options.elem.data(MOD_INDEX)) return;

    //  click 
    options.elem.on('click', function () {
      if (that.isFile()) return;
      $(this).trigger('upload.start');
    });

    //  drop 
    if (options.drag) {
      options.elem
        .on('dragover', function (e) {
          e.preventDefault();
          $(this).trigger('upload.over');
        })
        .on('dragleave', function (e) {
          $(this).trigger('upload.leave');
        })
        .on('drop', function (e) {
          e.preventDefault();
          $(this).trigger('upload.drop', e);
        });
    }

    //  click 
    options.bindAction.on('click', function () {
      $(this).trigger('upload.action');
    });

    // 
    options.elem.data(MOD_INDEX, options.id);
  };

  /**
   * 
   */
  upload.util = {
    /**
     * 
     * @param {number | string} size -
     * @param {number} [precision] - 
     * @return {string}
     */
    parseSize: function (size, precision) {
      precision = precision || 2;
      if (null == size || !size) {
        return '0';
      }
      var unitArr = ['Bytes', 'Kb', 'Mb', 'Gb', 'Tb', 'Pb', 'Eb', 'Zb', 'Yb'];
      var index;
      var formatSize = typeof size === 'string' ? parseFloat(size) : size;
      index = Math.floor(Math.log(formatSize) / Math.log(1024));
      size = formatSize / Math.pow(1024, index);
      size = size % 1 === 0 ? size : parseFloat(size.toFixed(precision)); //
      return size + unitArr[index];
    },
    /**
     *  JQueryDeferred 
     */
    promiseLikeResolve: function (value) {
      var deferred = $.Deferred();

      if (value && typeof value.then === 'function') {
        value.then(deferred.resolve, deferred.reject);
      } else {
        deferred.resolve(value);
      }
      return deferred.promise();
    }
  };

  // 
  thisModule.that = {}; // 

  // 
  thisModule.getThis = function (id) {
    var that = thisModule.that[id];
    if (!that)
      hint.error(
        id
          ? MOD_NAME + " instance with ID '" + id + "' not found"
          : 'ID argument required'
      );
    return that;
  };

  // 
  upload.render = function (options) {
    var inst = new Class(options);
    return thisModule.call(inst);
  };

  exports(MOD_NAME, upload);
});
