/**
 * util 
 */

layui.define(['lay', 'i18n', 'jquery'], function (exports) {
  'use strict';

  var $ = layui.$;
  var hint = layui.hint();
  var i18n = layui.i18n;
  var lay = layui.lay;

  //  dayjs
  // https://github.com/iamkun/dayjs/blob/v1.11.9/src/constant.js#L30
  var REGEX_FORMAT =
    /\[([^\]]+)]|y{1,4}|M{1,2}|d{1,2}|H{1,2}|h{1,2}|a|A|m{1,2}|s{1,2}|SSS/g;
  var REGEX_PARSE =
    /^(\d{4})[-/]?(\d{1,2})?[-/]?(\d{0,2})[T\s]*(\d{1,2})?:?(\d{1,2})?:?(\d{1,2})?[.:]?(\d+)?$/i;

  // 
  var util = {
    // 
    fixbar: function (options) {
      var ELEM = 'layui-fixbar';
      var $doc = $(document);

      // 
      options = $.extend(
        true,
        {
          target: 'body', // fixbar 
          bars: [], //  bar 
          default: true, //  bar
          margin: 160, //  top bar 
          duration: 320 // top bar 
        },
        options
      );

      // 
      var $target = $(options.target);

      // 
      var $scroll = options.scroll
        ? $(options.scroll)
        : $(options.target === 'body' ? $doc : $target);

      // 
      if (options['default']) {
        // 
        if (options.bar1) {
          options.bars.push({
            type: 'bar1',
            icon: 'layui-icon-chat'
          });
        }
        if (options.bar2) {
          options.bars.push({
            type: 'bar2',
            icon: 'layui-icon-help'
          });
        }
        //  top bar
        options.bars.push({
          type: 'top',
          icon: 'layui-icon-top'
        });
      }

      var elem = $('<ul>').addClass(ELEM);
      var elemTopBar;

      //  bars 
      layui.each(options.bars, function (i, item) {
        var elemBar = $('<li class="layui-icon">');

        //  bar 
        elemBar
          .addClass(item.icon)
          .attr({
            'lay-type': item.type,
            style:
              item.style ||
              (options.bgcolor ? 'background-color: ' + options.bgcolor : '')
          })
          .html(item.content);

        // bar 
        elemBar.on('click', function () {
          var type = $(this).attr('lay-type');
          if (type === 'top') {
            (options.target === 'body' ? $('html,body') : $scroll).animate(
              {
                scrollTop: 0
              },
              options.duration
            );
          }
          typeof options.click === 'function' && options.click.call(this, type);
        });

        // 
        if (layui.type(options.on) === 'object') {
          layui.each(options.on, function (eventName, callback) {
            elemBar.on(eventName, function () {
              var type = $(this).attr('lay-type');
              typeof callback === 'function' && callback.call(this, type);
            });
          });
        }

        //  top bar 
        if (item.type === 'top') {
          elemBar.addClass('layui-fixbar-top');
          elemTopBar = elemBar;
        }

        elem.append(elemBar); //  bar 
      });

      //  fixbar
      $target.find('.' + ELEM).remove();

      //  fixbar 
      typeof options.css === 'object' && elem.css(options.css);
      $target.append(elem);

      // top bar 
      if (elemTopBar) {
        var lock;
        var setTopBar = (function setTopBar() {
          var top = $scroll.scrollTop();
          if (top >= options.margin) {
            lock || (elemTopBar.show(), (lock = 1));
          } else {
            lock && (elemTopBar.hide(), (lock = 0));
          }
          return setTopBar;
        })();
      }

      //  scrollbar  fixbar 
      var timer;
      $scroll.on('scroll', function () {
        if (!setTopBar) return;
        clearTimeout(timer);
        timer = setTimeout(function () {
          setTopBar();
        }, 100);
      });
    },

    // 
    countdown: function (options) {
      // var that = this;

      // 
      options = $.extend(
        true,
        {
          date: new Date(),
          now: new Date()
        },
        options
      );

      // 
      var args = arguments;
      if (args.length > 1) {
        options.date = new Date(args[0]);
        options.now = new Date(args[1]);
        options.clock = args[2];
      }

      // 
      var inst = {
        options: options,
        clear: function () {
          // 
          clearTimeout(inst.timer);
        },
        reload: function (opts) {
          // 
          this.clear();
          $.extend(
            true,
            this.options,
            {
              now: new Date()
            },
            opts
          );
          count();
        }
      };

      typeof options.ready === 'function' && options.ready();

      // 
      var count = (function fn() {
        var date = new Date(options.date);
        var now = new Date(options.now);
        var countTime = (function (time) {
          return time > 0 ? time : 0;
        })(date.getTime() - now.getTime());
        var result = {
          d: Math.floor(countTime / (1000 * 60 * 60 * 24)), // 
          h: Math.floor(countTime / (1000 * 60 * 60)) % 24, // 
          m: Math.floor(countTime / (1000 * 60)) % 60, // 
          s: Math.floor(countTime / 1000) % 60 // 
        };
        var next = function () {
          now.setTime(now.getTime() + 1000);
          options.now = now;
          count();
        };

        // 
        if (args.length > 1) result = [result.d, result.h, result.m, result.s];

        //  - 
        inst.timer = setTimeout(next, 1000);
        typeof options.clock === 'function' && options.clock(result, inst);

        // 
        if (countTime <= 0) {
          clearTimeout(inst.timer);
          typeof options.done === 'function' && options.done(result, inst);
        }

        return fn;
      })();

      return inst;
    },

    // 
    timeAgo: function (time, onlyDate) {
      var that = this;
      var arr = [[], []];
      var stamp = new Date().getTime() - new Date(time).getTime();

      // 
      if (stamp > 1000 * 60 * 60 * 24 * 31) {
        stamp = new Date(time);
        arr[0][0] = that.digit(stamp.getFullYear(), 4);
        arr[0][1] = that.digit(stamp.getMonth() + 1);
        arr[0][2] = that.digit(stamp.getDate());

        // 
        if (!onlyDate) {
          arr[1][0] = that.digit(stamp.getHours());
          arr[1][1] = that.digit(stamp.getMinutes());
          arr[1][2] = that.digit(stamp.getSeconds());
        }
        return arr[0].join('-') + ' ' + arr[1].join(':');
      }

      // 30 
      if (stamp >= 1000 * 60 * 60 * 24) {
        return i18n.$t('util.timeAgo.days', {
          days: (stamp / 1000 / 60 / 60 / 24) | 0
        });
      } else if (stamp >= 1000 * 60 * 60) {
        return i18n.$t('util.timeAgo.hours', {
          hours: (stamp / 1000 / 60 / 60) | 0
        });
      } else if (stamp >= 1000 * 60 * 3) {
        // 3 
        return i18n.$t('util.timeAgo.minutes', {
          minutes: (stamp / 1000 / 60) | 0
        });
      } else if (stamp < 0) {
        return i18n.$t('util.timeAgo.future');
      } else {
        return i18n.$t('util.timeAgo.justNow');
      }
    },

    // 
    digit: function (num, length) {
      var str = '';
      num = String(num);
      length = length || 2;
      for (var i = num.length; i < length; i++) {
        str += '0';
      }
      return num < Math.pow(10, length) ? str + (num | 0) : num;
    },

    // 
    toDateString: function (time, format, options) {
      //  null 
      if (time === null || time === '') return '';

      var that = this;

      var normalizeDate = function (date) {
        if (typeof date === 'undefined') {
          return new Date();
        }
        if (!isNaN(date)) {
          return new Date(typeof date === 'string' ? parseInt(date) : date);
        }
        if (typeof date === 'string' && !/Z$/i.test(date)) {
          var d = date.match(REGEX_PARSE);
          if (d) {
            var m = d[2] - 1 || 0;
            var ms = (d[7] || '0').substring(0, 3);
            return new Date(
              d[1],
              m,
              d[3] || 1,
              d[4] || 0,
              d[5] || 0,
              d[6] || 0,
              ms
            );
          }
        }

        return new Date(date);
      };
      var date = normalizeDate(time);

      if (!date.getDate())
        return (
          hint.error(
            'Invalid millisecond for "util.toDateString(millisecond)"'
          ),
          ''
        );

      var years = date.getFullYear();
      var month = date.getMonth();
      var days = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      var milliseconds = date.getMilliseconds();

      var defaultMeridiem = i18n.$t('util.toDateString.meridiem');

      var meridiem = (options && options.customMeridiem) || defaultMeridiem;

      var matches = {
        yy: function () {
          return String(years).slice(-2);
        },
        yyyy: function () {
          return that.digit(years, 4);
        },
        M: function () {
          return String(month + 1);
        },
        MM: function () {
          return that.digit(month + 1);
        },
        d: function () {
          return String(days);
        },
        dd: function () {
          return that.digit(days);
        },
        H: function () {
          return String(hours);
        },
        HH: function () {
          return that.digit(hours);
        },
        h: function () {
          return String(hours % 12 || 12);
        },
        hh: function () {
          return that.digit(hours % 12 || 12);
        },
        A: function () {
          return meridiem(hours, minutes);
        },
        m: function () {
          return String(minutes);
        },
        mm: function () {
          return that.digit(minutes);
        },
        s: function () {
          return String(seconds);
        },
        ss: function () {
          return that.digit(seconds);
        },
        SSS: function () {
          return that.digit(milliseconds, 3);
        }
      };

      format = format || 'yyyy-MM-dd HH:mm:ss';

      return format.replace(REGEX_FORMAT, function (match, $1) {
        return $1 || (matches[match] && matches[match]()) || match;
      });
    },

    //  html
    escape: lay.escape,

    //  html
    unescape: lay.unescape,

    // 
    openWin: function (options) {
      var win;
      options = options || {};
      win =
        options.window ||
        window.open(options.url || '', options.target, options.specs);
      if (options.url) return;
      win.document.open('text/html', 'replace');
      win.document.write(options.content || '');
      win.document.close();
    },

    // 
    toVisibleArea: function (options) {
      options = $.extend(
        {
          margin: 160, // 
          duration: 200, // 
          type: 'y' // x y 
        },
        options
      );

      if (!options.scrollElem[0] || !options.thisElem[0]) return;

      var scrollElem = options.scrollElem; // 
      var thisElem = options.thisElem; // 
      var vertical = options.type === 'y'; // 
      var SCROLL_NAME = vertical ? 'scrollTop' : 'scrollLeft'; // 
      var OFFSET_NAME = vertical ? 'top' : 'left'; // 
      var scrollValue = scrollElem[SCROLL_NAME](); // 
      var size = scrollElem[vertical ? 'height' : 'width'](); // 
      var scrollOffset = scrollElem.offset()[OFFSET_NAME]; // 
      var thisOffset = thisElem.offset()[OFFSET_NAME] - scrollOffset; // 
      var obj = {};

      // 
      if (thisOffset > size - options.margin || thisOffset < options.margin) {
        obj[SCROLL_NAME] = thisOffset - size / 2 + scrollValue;
        scrollElem.animate(obj, options.duration);
      }
    },

    /**
     * 
     * @param {string} [attr="lay-on"] - 
     * @param {Object.<string, Function>} events - 
     * @param {Object} [options] - 
     * @param {(string|HTMLElement|JQuery)} [options.elem="body"] - 
     * @param {string} [options.trigger="click"] - 
     * @returns {Object}  events 
     */
    on: function (attr, events, options) {
      //  object  attr
      if (typeof attr === 'object') {
        options = events || {};
        events = attr;
        attr = options.attr || 'lay-on'; // 
      }

      // 
      options = $.extend(
        {
          elem: 'body',
          trigger: 'click'
        },
        typeof options === 'object'
          ? options
          : {
              trigger: options // 
            }
      );

      var elem = (options.elem = $(options.elem));
      var attrSelector = '[' + attr + ']';
      var DATANAME = 'UTIL_ON_DATA'; //  data-* 

      if (!elem[0]) return; // 

      //  data 
      if (!elem.data(DATANAME)) {
        elem.data(DATANAME, {
          events: {}
        });
      }

      //  data 
      var dataCache = elem.data(DATANAME);

      //  attr  trigger  key
      var key = attr + '_' + options.trigger;

      //  key 
      events = dataCache.events[key] = $.extend(
        true,
        dataCache.events[key],
        events
      );

      // 
      var trigger = options.trigger + '.lay_util_on';
      elem.off(trigger, attrSelector);

      // 
      elem.on(trigger, attrSelector, function (e) {
        var othis = $(this);
        var attrValue = othis.attr(attr);
        typeof events[attrValue] === 'function' &&
          events[attrValue].call(this, othis, e);
      });

      return events;
    }
  };

  // 
  util.event = util.on;

  // 
  exports('util', util);
});
